import React from "react"
import './DLCCards.css'
 function DCards(props){
 
     
     return(
        <div className="DLC-card">
        <img src={props.ImgUrl}/>
        <h3>{props.description}</h3>
        <p>Price: {props.price}€</p>
        <p>Release date: {props.Rdate}</p>
    </div>
     )
 } 


export default DCards